import { useState, useEffect } from "react";
import { Link } from 'react-router-dom';

const UserCard = ({ nft, walletAddress }) => {
  const [username, setUsername] = useState("");
  const [socialMediaName, setSocialMediaName] = useState("");
  const [userCardId, setUserCardId] = useState("");
  const [displaySavedData, setDisplaySavedData] = useState(false);
  const [socialMediaImage, setSocialMediaImage] = useState(""); // Add state for the social media image
  

  // Load the data from the server
  const loadData = async () => {
    try {
      const response = await fetch(`http://localhost:5004/user-cards/${nft.id}`);
      if (!response.ok) {
        throw new Error("Error fetching user card data.");
      }
      const latestUserCard = await response.json();
      if (latestUserCard) {
        setUsername(latestUserCard.username);
        setSocialMediaName(latestUserCard.socialMediaName);
        setUserCardId(latestUserCard._id);
        setDisplaySavedData(true);
      }
    } catch (error) {
      console.error("Error:", error.message);
    }
  };

  // Load data when the component mounts
  useEffect(() => {
    loadData();
  }, []);

  if (!nft) {
    return null;
  }

  const cardStyle = {
    boxShadow: "0 4px 6px rgba(0, 0, 0, 0.1)",
    borderRadius: "10px",
    padding: "20px",
    backgroundColor: "white",
    maxWidth: "200px",
    margin: "10px",
    textAlign: "center",
    
  };

  const imgStyle = {
    width: "100%",
    height: "auto",
    borderRadius: "5px",
    marginBottom: "10px",
    transition: "transform 0.2s ease-in-out",
  };
  
  const hoverStyle = {
    transform: "scale(1.05)",
  };
  // Save the data to the server
  // Save the data to the server
const saveData = async () => {
  try {
    let method = "POST";
    let url = "http://localhost:5004/user-cards";
    if (userCardId !== "") {
      method = "PUT";
      url = `http://localhost:5004/user-cards/${userCardId}`; // Include the userCardId in the URL
    }
    const response = await fetch(url, {
      method,
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        tokenId: nft.id,
        username,
        socialMediaName,
      }),
    });
    if (!response.ok) {
      throw new Error("Error saving user card data.");
    }
    const data = await response.json();
    setUserCardId(data._id);
  } catch (error) {
    console.error("Error:", error.message);
  }
  setDisplaySavedData(true); // Show the saved data after saving
};


  // Handle form submission
  const handleSubmit = (e) => {
    e.preventDefault();
    saveData();
  };
  if (!nft) {
    return null;
  }
  return (
    <div className="user-card" style={cardStyle} aria-label="User Card">
    
<Link to={nft.permalink} target="_blank" rel="noopener noreferrer">
  <img src={nft.image_url} alt={nft.name} style={imgStyle} />
</Link>
      <p>{nft.name}</p>
    
      {/*
      <form onSubmit={handleSubmit}>
        <label>
          Username:
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
          />
        </label>
        <label>
          Social Media Name:
          <input
        type="text"
        value={socialMediaName}
        onChange={(e) => setSocialMediaName(e.target.value)}
      />
    </label>
    <button type="submit">Save</button>
  </form>
  */}
  {displaySavedData && (
    <div className="saved-data">
      <p>
        <strong>Username:</strong> {username}
      </p>
      <p>
        <strong>Social Media Name:</strong> {socialMediaName}
      </p>
    </div>
  )}
</div>

  );  
};
export default UserCard;
