import axios from "axios";

const fetchNFTData = async (walletAddress) => {
  const apiUrl = "https://api.opensea.io/api/v1";

  const contractAddresses = [
    "0xc88C928e2b7c0cC531D814f9A9C57cce7271a0e1",
    "0x219e1fd6701256920dB7a4A021ace7B84d1059e4",
    "0x8B926195A5E9f5b9DEAAE18D9F27Cd58Ec642466",
  ];

  const nfts = [];

  try {
    for (const contractAddress of contractAddresses) {
      const response = await axios.get(`${apiUrl}/assets`, {
        params: {
          owner: walletAddress,
          asset_contract_address: contractAddress,
          limit: 25,
        },
      });

      if (response.data.assets.length > 0) {
        const nftData = response.data.assets;
        nfts.push(...nftData);
      }
    }

    return nfts;
  } catch (error) {
    console.error("Error fetching NFT data:", error);
    return [];
  }
};

export default fetchNFTData;
