import axios from "axios";
const apiUrl = "https://api.opensea.io/api/v1";

const checkNFTOwnership = async (walletAddress) => {
  const apiUrl = "https://api.opensea.io/api/v1";
  const contractAddress = "0x8415D610f19641d97c088b532663CDCA3dc58826";

  try {
    const response = await axios.get(`${apiUrl}/assets`, {
      params: {
        owner: walletAddress,
        asset_contract_address: contractAddress,
        limit: 1,
      },
    });

    return response.data.assets.length > 0;
  } catch (error) {
    console.error("Error checking NFT ownership:", error);
    return false;
  }
};

export default checkNFTOwnership;
