import { useState, useEffect } from "react";
import { BrowserRouter as Router, Route, Link, Routes } from "react-router-dom";
import checkNFTOwnership from "./utils/checkNFTOwnership";
import UserCard from "./components/UserCard";
import HomePage from "./components/HomePage";
import NFTCollection from "./components/NFTCollection";
import Artists from './components/Artists';
import Story from './components/Story';
import GamePage from "./components/GamePage";
import "./App.css";
import social1 from './images/social1.png'; // Import the first social media image
import social2 from './images/social2.png'; // Import the second social media image

import social3 from './images/social3.png'; // Import the second social media image
import logo1 from './images/logo1.png'; // Import the first logo image
import logo2 from './images/logo2.png'; // Import the second logo image
import logo3 from './images/logo3.png'; // Import the second logo image
import MintingPage from "./components/MintingPage"; // Add this import at the top


function App() {
  const [walletAddress, setWalletAddress] = useState("");
  const [isOwner, setIsOwner] = useState(null);

  const signInWithEthereum = async () => {
    if (window.ethereum) {
      try {
        const accounts = await window.ethereum.request({ method: "eth_requestAccounts" });
        setWalletAddress(accounts[0]);
        const ownsNFT = await checkNFTOwnership(accounts[0]);
        setIsOwner(ownsNFT);
      } catch (error) {
        console.error("Error connecting to Ethereum wallet:", error);
      }
    } else {
      alert("Please install MetaMask or another Ethereum wallet.");
    }
  };

  return (
    <Router>
      <div className="App">
        <header>
          <div className="header-top">
            <div className="logo-container">
              <img src={logo1} alt="Logo 1" className="logo-1" />
              <img src={logo2} alt="Logo 2" className="logo-2" />
            </div>
            <div className="sign-in-container">
              {walletAddress ? (
                <UserCard walletAddress={walletAddress} />
              ) : (
                <button className="sign-in-button" onClick={signInWithEthereum}>
                  web3 login
                </button>
              )}
            </div>
          </div>
          <nav className="menu">
            <ul>
              <li>
                <Link to="/" className="menu-item">Home</Link>
              </li>
              {walletAddress && (
                <li>
                  <Link to="/nft-collection" className="menu-item">my CP NFTs</Link>
                </li>
              )}
              <li>
                <a href="https://opensea.io/collection/cpcpc1" target="_blank" rel="noopener noreferrer" className="menu-item">1st community photo collection - Opensea</a>
              </li>
              <li>
                <a href="https://www.camerapersoncpc.xyz/" target="_blank" rel="noopener noreferrer" className="menu-item">mint the 1st collection</a>
              </li>
              <li>
                <Link to="/artists" className="menu-item">the artists…</Link>
              </li>
              <li>
                <Link to="/story" className="menu-item">the story…</Link>
              </li>
              <li>
              <Link to="/mint" className="menu-item">the lost ones...</Link>
              </li>
              <li>
                <Link to="/GamePage" className="menu-item">chasing sunsets game</Link>
              </li>

            </ul>
          </nav>
          <div className="social-container">
            <a href="https://www.twitter.com/CameraPersonNFT" target="_blank" rel="noopener noreferrer">
              <img src={social1} alt="twitter" className="social-icon" />
            </a>
            <a href="https://opensea.io/collection/camera-person-nft" target="_blank" rel="noopener noreferrer">
              <img src={social2} alt="opensea" className="social-icon" />
            </a>
            <a href="https://discord.gg/PqkJGFr3pj" target="_blank" rel="noopener noreferrer">
              <img src={social3} alt="discord" className="social-icon" />
            </a>
            {/* Add more social media icons with the same format */}
          </div>
        </header>

        <main>
          <Routes>
            <Route path="/" element={<HomePage signInWithEthereum={signInWithEthereum} />} />
            {walletAddress && <Route path="/nft-collection" element={<NFTCollection walletAddress={walletAddress} />} />}
            <Route path="/artists" element={<Artists />} />
            <Route path="/story" element={<Story />} />
            <Route path="/GamePage" element={<GamePage />} />
            <Route path="/mint" element={<MintingPage walletAddress={walletAddress} />} />

          </Routes>
        </main>

        <footer className="alignfull site-footer wp-block-template-part sticky-footer">
          <img src={logo3} alt="Twitter" className="social-icon" />
        </footer>
      </div>
    </Router>
  );
}

export default App;