// src/components/MintingPage.js

import React, { useState } from "react";
import { ethers } from "ethers";
import YOUR_ABI from "./myABI.json";
import "./MintingPage.css";

const MintingPage = ({ walletAddress }) => {
  const [status, setStatus] = useState("");
  const [mintAmount, setMintAmount] = useState(1);

  const mintNFT = async () => {
    if (!window.ethereum) {
      setStatus("Please install MetaMask or another Ethereum wallet.");
      return;
    }

    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const contract = new ethers.Contract(
        "0x8B926195A5E9f5b9DEAAE18D9F27Cd58Ec642466",
        YOUR_ABI,
        signer
      );

      // Set the mintAmount and mintCost (in ETH) values here
      const mintAmount = 1;
      const mintCost = 0.02;

      // Calculate the transaction cost (in Wei)
      const txCost = mintAmount * mintCost;
      const txParamsOverride = {
        value: ethers.utils.parseEther(txCost.toString())
      };

      // Replace `publicMint` with the correct function name from your smart contract
      const transaction = await contract.publicMint(mintAmount, txParamsOverride);
      setStatus("Minting in progress...");

      await transaction.wait();
      setStatus("Minting complete!");
    } catch (error) {
      console.error("Error minting NFT:", error);
      
      if (error.message.includes("insufficient funds")) {
        console.log("Insufficient funds for the transaction.");
        setStatus("Insufficient funds for the transaction. Please add more ETH to your wallet.");
      } else {
        setStatus("Error minting NFT. Check console for details.");
      }
    }
  };   

  const incrementMintAmount = () => {
    setMintAmount(mintAmount + 1);
  };

  const decrementMintAmount = () => {
    if (mintAmount > 1) {
      setMintAmount(mintAmount - 1);
    }
  };


  return (
<div className="minting-container" style={{ paddingBottom: "250px" }}>
      <h1>Camera Person: THE LOST ONES</h1>
      <h2>2222 Camera Persons have been found roaming a nearby land....</h2>
      <h3>
              Camera Person: The Lost Ones is not only a discovery of Camera Person's lore but it is also an expansion
              of the photography collective for production of future bodies of work.
            </h3>
            <h4>
              The Lost Ones' owners will have the ability to use their NFTs to submit work to future community
              photography collections.
            </h4>
      <div className="card">
        {walletAddress ? (
          <>
            <div className="minting-controls">
              <button className="menu-item" onClick={decrementMintAmount}>-</button>
              <span>{mintAmount}</span>
              <button className="menu-item" onClick={incrementMintAmount}>+</button>
            </div>
            <button className="menu-item" onClick={mintNFT}>Mint {mintAmount} NFT(s) - 0.02 ETH / NFT</button>
            <p>{status}</p>
          </>
        ) : (
          <p>Please connect your wallet to mint NFTs.</p>
        )}
      </div>
    </div>
  );
};
export default MintingPage;
